import React from "react";
import useTableDataContext from "../context/TableDataContext";
import Table from "./TableComponents/Table.jsx";

const SchemaDesigner = ({ svgRef, showToastMessage, contextIndex, setContextIndex, containerRef}) => {
  const { currentProject,restrictedViewEnabled } = useTableDataContext();
  const { parsedSchemaObjects } = currentProject;

  if (!Array.isArray(parsedSchemaObjects)) {
    return null;
  }

  return (
    <>
      {parsedSchemaObjects.map((schema, index) => {
        if(!restrictedViewEnabled){
          return (
            <Table
              key={`table-${schema.name}`}
              schema={schema}
              index={index}
              svgRef={svgRef}
              showToastMessage={showToastMessage}
              contextIndex={contextIndex}
              setContextIndex={setContextIndex}
              containerRef={containerRef}
            />
          );
        }
        else if(schema.visible){
          return (
            <Table
              key={`table-${schema.name}`}
              schema={schema}
              index={index}
              svgRef={svgRef}
              showToastMessage={showToastMessage}
              contextIndex={contextIndex}
              setContextIndex={setContextIndex}
              containerRef={containerRef}
            />
          );
        }
      })}
    </>
  );
};

export default React.memo(SchemaDesigner);
