import React, { useState } from "react";
import { Offcanvas, ListGroup, Button } from "react-bootstrap";
import {
  AiOutlineEye,
  AiOutlineEyeInvisible,
  AiOutlineLeft,
} from "react-icons/ai";
import { CiViewTable } from "react-icons/ci";
import useTableDataContext from "../context/TableDataContext";

const SidePane = () => {
  const { currentProject, dispatchCurrentProject,showPane,setShowPane } = useTableDataContext();
  const { parsedSchemaObjects } = currentProject;

  const toggleVisibility = (index) => {
    dispatchCurrentProject({
      type: "toggle_table_visibility",
      tableIndex: index,
    });
  };

  return (
    <>
      <Offcanvas
        backdrop={false}
        scroll={true}
        show={showPane}
        onHide={()=>setShowPane(false)}
        placement="end"
      >
        <Offcanvas.Header closeButton>
          <Offcanvas.Title>Tables</Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body>
          <ListGroup>
            {parsedSchemaObjects.map((schema, index) => (
              <ListGroup.Item
                key={index}
                className="d-flex justify-content-between align-items-center"
              >
                {schema.name}
                <Button variant="link" onClick={() => toggleVisibility(index)}>
                  {schema.visible ? (
                    <AiOutlineEyeInvisible size={20} />
                  ) : (
                    <AiOutlineEye size={20} />
                  )}
                </Button>
              </ListGroup.Item>
            ))}
          </ListGroup>
        </Offcanvas.Body>
      </Offcanvas>
    </>
  );
};

export default SidePane;
