import React from "react";

const Arrow = ({ from, to, color, strokeWidth, cp1, cp2, markerSize }) => {
  return (
    <>
      <defs>
        <marker
          id="arrowHead"
          markerWidth={markerSize}
          markerHeight={markerSize}
          refX={markerSize}
          refY={markerSize*0.5}
          orient="auto"
          >
            <polygon
            points={`0 0, ${markerSize} ${markerSize*0.5}, 0 ${markerSize}`}
            fill={color}
            />
        </marker>
      </defs>
      <g>
        <circle cx={from.x} cy={from.y} r={4} fill={color} />
        <path 
          d={`M${from.x},${from.y} C${cp1.x},${cp1.y} ${cp2.x},${cp2.y} ${to.x},${to.y}`}
          stroke={color}
          fill="transparent"
          strokeWidth={strokeWidth}
          markerEnd="url(#arrowHead)"/>
      </g>
    </>
  )
}

export default Arrow;
