import React, { useMemo } from "react";
import styles, { defaultFontSize } from "../../styles/styles";
import useTableDataContext from "../../context/TableDataContext";
import { Button } from "react-bootstrap";

const TableField = ({
  xRectPos,
  field,
  fieldTextX,
  fieldTypeX,
  fieldY,
  highlighted,
  cumulativeHeight,
  height,
  width,
  schemaName,
}) => {
  const { zoom, currentProject, dispatchCurrentProject } =
    useTableDataContext();
  const { parsedSchemaObjects } = currentProject;
  const fontSize = useMemo(() => defaultFontSize * zoom, [zoom]);

  const isAnyFieldReferences = parsedSchemaObjects.some((schema) => {
    return schema.fields.some((f) => {
      return f.fk_table === schemaName && f.fk_field === field.name;
    });
  });

  const handleButtonClick = () => {
    dispatchCurrentProject({
      type: "TOGGLE_REFERENCED_TABLES_VISIBILITY",
      schemaName,
      field,
    });
  };

  const toggleReferencedTablesVisibility =() =>{
    dispatchCurrentProject({
      type:"toggle_referenced_tables_visibility",
      schemaName,
      field
    })
  }

  const toggleReferencingTableVisibility = () => {
    dispatchCurrentProject({
      type:"toggle_referencing_table_visibility",
      field
    })
  }

  const fieldElements = useMemo(
    () => (
      <g key={`field${field.name}`}>
        <rect
          x={xRectPos}
          y={cumulativeHeight}
          width={width}
          height={height}
          fill={highlighted ? "lightgray" : field.color || "lightgray"}
        />
        {isAnyFieldReferences && (
          <text
            x={fieldTextX - 12 * zoom}
            y={fieldY - styles.fieldCellHeight * 0.25 * zoom}
            fontFamily="monospace"
            fontSize={fontSize * 2}
            fill="black"
            textAnchor="middle"
            style={{
              cursor:"pointer",
            }}
            dominantBaseline="hanging"
            onClick={toggleReferencedTablesVisibility}
          >
            &rarr;
          </text>
        )}
        <text
          x={fieldTextX}
          y={fieldY}
          fontFamily="monospace"
          fontSize={fontSize}
          fill="black"
          fontWeight={field.pk ? "bold" : "normal"}
          dominantBaseline="hanging"
        >
          {field.name}
        </text>
        <text
          x={fieldTypeX}
          y={fieldY}
          fontFamily="monospace"
          fontSize={fontSize}
          fill="black"
          textAnchor="end"
          fontWeight={field.pk ? "bold" : "normal"}
          dominantBaseline="hanging"
        >
          {field.type}
        </text>
        {field.fk && (
          <text
            x={fieldTypeX + 20 * zoom}
            y={fieldY - styles.fieldCellHeight * 0.25 * zoom}
            fontFamily="monospace"
            fontSize={fontSize * 2}
            style={{
              cursor:"pointer",
            }}
            fill="black"
            textAnchor="end"
            dominantBaseline="hanging"
            onClick={toggleReferencingTableVisibility}
          >
            &rarr;
          </text>
        )}
      </g>
    ),
    [
      xRectPos,
      cumulativeHeight,
      width,
      height,
      highlighted,
      fieldTextX,
      fieldY,
      fieldTypeX,
      fontSize,
      handleButtonClick,
      isAnyFieldReferences,
    ]
  );

  return fieldElements;
};

export default TableField;
